:root {
    --bg-color: #000000;
    /* Background color */
    --header-bg-color: #0c0c0c;
    /* Header background color */
    --text-color: #fff;
    /* Main text color */
    --secondary-text-color: #aaa;
    /* Secondary text color */
    --header-text-color: #95D721;
    /* Header text color */
    --secondary-header-text-color: var(--secondary-text-color);
    /* Secondary header text color */

    --link-color: #95D721;
    /* Link color */
    --link-hover-color: yellowgreen;
    /* Link hover color */

    --button-bg-color: #95D721;
    /* Button background color */
    --button-hover-bg-color: yellowgreen;
    /* Button hover background color */
    --font-family: "Roboto Condensed", 'Trebuchet MS', 'Tahoma', sans-serif;
}


/* Reset margins and paddings for body and html */
html,
body {
    margin: 0;
    padding: 0;
    height: 100%;
    /* Full height for html and body */
    background-color: var(--bg-color);
    /* Dark grey background */
}

/* General page styling */
.page-container {
    background-color: var(--bg-color);
    /* Dark grey background */
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var(--text-color);
    font-family: var(--font-family);
    width: 100%;
    /* Ensure full width */
}

/* Header */
.header {
    position: sticky;
    top: 0;
    /* Stick to the top of the viewport */
    /* Ensure it appears above other content */
    width: 100%;
    /* Full width for the header */
    background-color: var(--header-bg-color);
    /* Darker grey background for header */
    padding: 5px 0;
    /* Vertical padding */
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.7);
    z-index: 9999;
    /* Ensure it appears above the video */
}

/* Header Content Wrapper */
.header-content {
    max-width: 1400px;
    /* Limit header contents to a max width */
    width: 100%;
    /* Full width for responsive */
    margin: 0 auto;
    /* Center the header contents */
    display: flex;
    /* Flexbox for alignment */
    justify-content: space-between;
    /* Space between items */
    align-items: center;
    /* Align items vertically */
}

.header {
    position: sticky;
    top: 0;
    width: 100%;
    background-color: var(--header-bg-color);
    padding: 5px 0;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.7);
    z-index: 9999;
}

.header-content {
    max-width: 1400px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    /* Required for absolutely positioning the center */
}

.header-left,
.header-right {
    display: flex;
    align-items: center;
}

/* Center header text absolutely in the middle of the header-content */
.header-center {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    color: var(--link-color);
    font-size: 14px;
    font-weight: bold;
    white-space: nowrap;
    /* Prevents text wrapping */
}

.header-right i {
    color: var(--text-color);
    font-size: 20px;
}

.header-right i:hover {
    color: var(--button-hover-bg-color);
}

.header-right a {
    margin-right: 5px;
    /* Adds 5px space to the right of each link */
}

.header-right a:last-child {
    margin-right: 0;
    /* Removes right margin from the last link to avoid extra spacing */
}

.header-left {
    display: flex;
    align-items: center;
}

.header-image {
    width: 60px;
    height: 50px;
}

.header-image-right {
    width: 90px;
    height: 20px;
}

.header-title {
    color: var(--text-color);
    font-size: 2em;
    margin: 0;
}


/* Social Icon Styling */
.social-icon {
    width: 24px;
    height: 24px;
    margin-left: 15px;
    filter: brightness(0) invert(1);
    /* Makes icon white */
}

/* Main content */
.main-content {
    display: flex;
    flex-direction: column;
    /* Stack sections vertically */
    width: 90%;
    /* Content width */
    max-width: 1400px;
    /* Limit width on larger screens */
    /* Centered on the page */
    margin: 0 auto 10px;
    /* top: 0, right: auto, bottom: 10px, left: auto */

}

/* Styling for the Twitch links */
.twitch-link,
.go-to-channel {
    color: var(--link-color);
    /* Set text color to lime green */
    text-decoration: none;
    /* Remove underline */
    transition: color 0.3s;
    /* Smooth transition for color change */
}

/* Change color on hover */
.twitch-link:hover,
.go-to-channel:hover {
    color: var(--link-hover-color);
    /* Change color to yellow-green on hover */
}


/* Container for Recruitment and Streams */
.recruitment-streams-container {
    display: flex;
    /* Flexbox for side-by-side layout */
    justify-content: space-between;
    /* Space out recruitment and streams */
    flex-wrap: wrap;
    /* Allow wrapping on smaller screens */

}

/* Recruitment Section */
.recruitment-section {
    width: 68%;
    /* Wider section for recruitment */
    background-color: var(--header-bg-color);
    /* Darker background for the section */
    border-radius: 5px;
    flex-shrink: 0;
    position: relative;
    min-height: 200px;
    max-height: 200px;

}

.latest-kill-carousel {
    width: 100%;
    height: 65vh;
    /* Half of the viewport height */
    margin-bottom: 20px;
    /* Space between carousel and next section */
    position: relative;
    overflow: hidden;
}

.carousel-image-container {
    height: 100%;
    /* Make the container fill the height */
    width: 100%;
    /* Ensure container takes up full width */
    display: flex;
    justify-content: center;
    align-items: center;
}

.carousel-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    /* Adjusts image to cover the container while maintaining aspect ratio */
    object-position: center;
    /* Centers the image within the container */
}



/* Streams Section */
.streams-section {
    width: 30%;
    background-color: var(--header-bg-color);
    border-radius: 5px;
    flex-shrink: 0;

}

/* Recruitment Section Header */
.recruitment-section h2 {
    color: var(--header-text-color);
    font-size: 16px;
}

/* Recruitment Info */
.recruitment-info,
.streams-section-info {
    margin-top: 5px;
    padding: 10px;
}

.recruitment-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 0;
}

.class-icon {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.class-name {
    font-size: 18px;
    flex-grow: 1;
    text-align: left;
    margin-left: 10px;
}

.priority {
    font-size: 16px;
    font-weight: bold;
}

.high-priority {
    color: #ff0000;
}

.medium-priority {
    color: #ffa601ad;
}

.low-priority {
    color: #11eb5a;
}

/* Apply button */
.apply-button {
    position: absolute;
    /* Use absolute positioning */
    top: 5px;
    /* Adjust as needed for spacing from the bottom */
    right: 10px;
    /* Adjust as needed for spacing from the right */
    padding: 10px 20px;
    font-size: 18px;
    background-color: #141414;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    color: white;
}

/* Apply button hover effect */
.apply-button:hover {
    color: limegreen;
}

/* Recruitment Section Header */
.recruitment-section h2,
.streams-section h2 {
    font-family: "Roboto Condensed", sans-serif;
    text-transform: uppercase;
    color: white;
    /* Default color for other words */
    margin: 0;
    font-size: 18px;
    letter-spacing: 0.5px;
    background: linear-gradient(to right, rgba(34, 139, 34, 0.08), rgba(20, 20, 20, 0));
    /* Softer green to darker, more blended */
    padding: 10px 20px;
    position: relative;
    display: inline-block;
    /* To avoid covering other elements */
}


/* Orange vertical line on the left */
.recruitment-section h2::before,
.streams-section h2::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 5px;
    /* Thickness of the vertical line */
    background-color: limegreen;
    /* Orange color */
}

/* Make the first word orange */
.recruitment-section h2 span.first-word,
.streams-section h2 span.first-word {
    color: #ff8a00;
    /* Orange color for the first word */
    font-weight: bold;
    /* Bold for emphasis */
}

/* New Styles for Role Icons and Stream Info */
.stream-info {
    display: flex;
    align-items: center;
    /* Align icon and name horizontally */

}

/* Styling for Stream Items */
.stream-item {
    display: flex;
    /* Flexbox for alignment */
    align-items: center;
    /* Align items vertically */
    margin-bottom: 5px;
    /* Space between items */
}

.stream-thumbnail {
    width: 100px;
    /* Width of the stream thumbnail */
    height: 60px;
    /* Height of the stream thumbnail */
    margin-right: 10px;
    /* Space between thumbnail and details */
    border-radius: 5px;
    /* Slightly round corners */
}

.stream-details {
    display: flex;
    /* Flexbox for stream details */
    flex-direction: column;
    /* Stack details vertically */
    justify-content: center;
    /* Center items vertically */
}

.stream-details h3 {
    font-size: 16px;
    /* Size of stream title */
    margin: 0;
    /* Remove default margin */
}

.stream-viewers {
    font-size: 14px;
    /* Size of viewer count */
    color: #aaa;
    /* Lighter color for viewers */
}

/* Styles for Offline Indicator */
.stream-offline {
    color: #ff4444;
    /* Color for offline status */
    font-weight: bold;
    /* Bold text */
}

.offline-section {
    margin-top: 10px;
    /* Space above offline section */
}

.offline-header {
    display: flex;
    justify-content: space-between;
    /* Align items with space between */
    align-items: center;
    /* Vertically center items */
    text-transform: uppercase;
    color: #fff;
    font-size: 16px;
    letter-spacing: 0.5px;

}

.image-placeholder-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.video-placeholder {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
    /* Ensures the entire image is visible */
}

.expand-button {
    background: none;
    border: none;
    font-size: 1.5em;
    cursor: pointer;
    margin-left: 10px;
    /* Spacing between the text and button */
    color: var(--link-color);
}

/* Hover effects */
.twitch-link {
    color: var(--link-color);
    /* Twitch brand color */
    text-decoration: none;
    font-weight: bold;
}

.twitch-link:hover {
    text-decoration: none;
}

.role-icon {
    width: 20px;
    /* Adjust as necessary */
    height: 20px;
    /* Adjust as necessary */
    margin-right: 5px;
    /* Adjust to add spacing between the icon and the name */
    vertical-align: middle;
    /* To align the icon with the text */
}

/* Tablet Adjustments */
@media (max-width: 1424px) {
    .header-title {
        font-size: 24px;
        /* Adjust title font size for tablets */
    }

    /* Header Content Wrapper */
    .header-content {
        width: 90%;
    }

    .header-center {
        font-size: 16px;
        /* Adjust center text size for tablets */
    }


    .recruitment-section {
        width: 60%;
        /* Full width on smaller screens */
    }

    .class-name {
        font-size: 18px;
        /* Larger class name for tablets */
    }

    .priority {
        font-size: 16px;
        /* Adjust priority text size for tablets */
    }

    .apply-button {
        width: auto;
        /* Set width to auto for a more compact button on tablets */
        padding: 10px 20px;
        /* Add padding for better touch targets */
    }

    .video-iframe {
        height: 400px;
        /* Adjust video height for tablets */
    }
}

/* Responsive Adjustments */
@media (max-width: 768px) {

    .recruitment-section,
    .streams-section {
        width: 99%;
        margin-bottom: 10px;
    }
    .latest-kill-carousel {
        width: 100%;
        height: 30vh;

        margin-bottom: 20px;

        position: relative;
        overflow: hidden;
    }

    .header-content {
        margin: 0 20px;
    }

    .header-center {
        display: none;
    }

    .recruitment-streams-container {
        flex-direction: column;

    }


}

@media (max-width: 480px) {
    .header-title {
        font-size: 16px;

    }

    .video-iframe {
        height: 200px;

    }

    .header-center {
        font-size: 12px;

    }

    .class-name {
        font-size: 16px;

    }

    .priority {
        font-size: 14px;

    }

}